import {ProfilePageType} from "./store";
import {Dispatch} from "redux";
import axios from "axios";
import {getStatus, updateStatus} from "../api/api";

export type ProfileType = {
    aboutMe: string,
    contacts: {
        facebook: null | string,
        website: null | string,
        vk: null | string,
        twitter: null | string,
        instagram: null | string,
        youtube: null | string,
        github: null | string,
        mainLink: null | string,
    },
    lookingForAJob: boolean,
    lookingForAJobDescription: null | string,
    fullName: null | string,
    userId: number | string,
    photos: {
        small: string,
        large: string,
    }
}
type ActionsType = ReturnType<typeof addPostAC>
    | ReturnType<typeof setUsersProfile>
| ReturnType<typeof setStatus>
| ReturnType<typeof deletePostAC>

const initialState: ProfilePageType = {
    posts: [
        {id: 1, message: "Hi, how are you?", likesCount: 5},
        {id: 2, message: "It is my first post!", likesCount: 10},
    ],
    profile: null,
    status: "",
}
const profileReducer = (state = initialState, action: ActionsType): ProfilePageType => {
    switch (action.type) {
        case "profile/ADD_POST":
            return {
                ...state,
                posts: [...state.posts, {id: 5, message: action.postText, likesCount: 0}],
            };
        case "profile/DELETE-POST":
            return {...state, posts: state.posts.filter(p => p.id != action.id)}
        case "profile/SET-USERS-PROFILE":
            return {...state, profile: action.profile}
        case "profile/SET-STATUS":
            return {...state, status: action.status}
        default:
            return state;
    }
}
export const addPostAC = (postText: string) => {
    return {
        type: "profile/ADD_POST",
        postText
    } as const
}
export const deletePostAC = (id: number) => {
    return {
        type: "profile/DELETE-POST",
        id
    } as const
}
export const setUsersProfile = (profile: ProfileType) => {
    return {
        type: "profile/SET-USERS-PROFILE",
        profile,
    } as const
}
export const setStatus = (status: string) => {
    return {
        type: "profile/SET-STATUS",
        status
    } as const
}
export const setUserProfileTC = (id: string) => {
    return (dispatch: Dispatch) => {
        if (!id) {
            id="18303";
        }
        axios.get(`https://social-network.samuraijs.com/api/1.0/profile/${id}`).then((response) => {
            dispatch(setUsersProfile(response.data))
        })
    }
}
export const setStatusTC = (id: number | string ) => {
    return (dispatch: Dispatch) => {
        if (!id) {

            id="18303";
        }
        getStatus(id).then((response) => {
            dispatch(setStatus(response.data))
        })
    }
}
export const updateStatusTC = (status: string) => {
    return (dispatch: Dispatch) => {
        updateStatus(status)
            .then(response => {
                if (response.data.resultCode === 0){
                    dispatch(setStatus(status));
                }
            })
    }
}
export default profileReducer;
