import {DialogsPageType} from "./store";
import {Dispatch} from "redux";
import {followUserAPI, getUsersAPI, unfollowUserAPI} from "../api/api";

export type LocationType = {
    city: string
    country: string
}
export type PhotosType = {
    small: any
    large: any
}
export type UserType = {
    name: string
    id: number
    uniqueUrlName: any
    photos: any
    status: any
    followed: boolean
}
export type UsersPageType = {
    items: Array<UserType>
    totalCount: number
    error: any,
    pageSize: number,
    currentPage: number
    isFetching: boolean
    followingInProgress: Array<number>
}
const initialState = {
    items: [],
    totalCount: 100,
    error: null,
    pageSize: 5,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
}
export const usersReducer = (state: UsersPageType = initialState, action: ActionsType): UsersPageType => {
    switch (action.type) {
        case "users/FOLLOW":
            return {
                ...state,
                items: state.items.map(item => item.id === action.userId ? {...item, followed: true} : item)
            }
        case "users/UNFOLLOW":
            return {
                ...state,
                items: state.items.map(item => item.id === action.userId ? {...item, followed: false} : item)
            }
        case "users/SET-USERS":
            return {...state, items: [...action.users]}
        case "users/SET-CURRENT-PAGE":
            return {...state, currentPage: action.currentPage}
        case "users/SET-TOTAL-COUNT":
            return {...state, totalCount: action.totalCount}
        case "users/TOGGLE-IS-FETCHING":
            return {...state, isFetching: action.isFetching}
        case "users/TOGGLE-FOLLOWING-PROGRESS":
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state;
    }
}

type ActionsType =
    ReturnType<typeof follow>
    | ReturnType<typeof unfollow>
    | ReturnType<typeof setUsers>
    | ReturnType<typeof setCurrentPage>
    | ReturnType<typeof setTotalCount>
    | ReturnType<typeof toggleIsFetching>
    | ReturnType<typeof toggleFollowingProgress>
export const follow = (userId: number) => {
    return {
        type: "users/FOLLOW",
        userId
    } as const
}
export const unfollow = (userId: number) => {
    return {
        type: "users/UNFOLLOW",
        userId
    } as const
}
export const setUsers = (users: Array<UserType>) => {
    return {
        type: "users/SET-USERS",
        users
    } as const
}
export const setCurrentPage = (currentPage: number) => {
    return {
        type: "users/SET-CURRENT-PAGE",
        currentPage
    } as const
}
export const setTotalCount = (totalCount: number) => {
    return {
        type: "users/SET-TOTAL-COUNT",
        totalCount
    } as const
}
export const toggleIsFetching = (isFetching: boolean) => {
    return {
        type: "users/TOGGLE-IS-FETCHING",
        isFetching,
    } as const
}
export const toggleFollowingProgress = (isFetching: boolean, userId: number) => {
    return {
        type: "users/TOGGLE-FOLLOWING-PROGRESS",
        isFetching,
        userId
    } as const
}
export const getUsers =(currentPage: number, pageSize: number) => {

    return (dispatch: Dispatch) => {
        dispatch(setCurrentPage(currentPage));
        dispatch(toggleIsFetching(true))
        getUsersAPI(currentPage, pageSize).then((data) => {

            dispatch(setTotalCount(data.totalCount))
            dispatch(setUsers(data.items));
            dispatch(toggleIsFetching(false));
        })
    }
}
export const unfollowUser = (id: number) => {
    return (dispatch: Dispatch) =>{
        dispatch(toggleFollowingProgress(true, id))
        unfollowUserAPI(id).then((data: any) => {
            if (data.resultCode === 0){
                dispatch(unfollow(id))
            }
            dispatch(toggleFollowingProgress(false, id))
        })
    }
}
export const followUser = (id: number) => {
    return (dispatch: Dispatch) =>{
        dispatch(toggleFollowingProgress(true, id))
        followUserAPI(id).then((data: any) => {
            if (data.resultCode === 0){
                dispatch(follow(id))
            }
            dispatch(toggleFollowingProgress(false, id))
        })
    }
}