import  axios from 'axios';
const instance = axios.create({
    withCredentials: true,
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    headers: {
        "API-KEY": "22ef31f8-5320-4166-8a28-b0a3a258ac17"
    }
})
export const getUsersAPI = (currentPage: number = 1, pageSize: number = 10) => instance.get(`users?page=${currentPage}&count=${pageSize}`)
.then((response) => response.data)
export const followUserAPI = (userId: number) => instance.post(`follow/${userId}`).then((response) => response.data);
export const unfollowUserAPI = (userId: number) => instance.delete(`follow/${userId}`).then((response) => response.data);
export const loggIn = () => instance.get(`auth/me`);
export const getStatus = (userId: number | string) =>instance.get(`profile/status/${userId}`);
export const updateStatus = (status: string) => instance.put(`profile/status`, {status})
export const sendLoginRequest = (email: string, password: string, rememberMe = false) => instance.post(`auth/login`, {email, password, rememberMe})
export const sendLogOutRequest = () => instance.delete(`auth/login`);