import React from "react";
import s from "./Users.module.css";
import userPhoto from "../../Graphics/Images/images.jpg";
import {UserType} from "../../redux/users-reducer";
import { NavLink } from "react-router-dom";
import axios from "axios";
import {followUserAPI, unfollowUserAPI} from "../../api/api";
import {Pagination} from "../common/Pagination/Pagination";

export type onPageChangeType = (pageNumber: number) => void
type UsersFCPropsType = {
    totalCount: number
    pageSize: number
    onPageChanged: (pageNumber: number) => void
    currentPage: number
    items: Array<UserType>
    follow: (userId: number) => void
    unfollow: (userId: number) => void
    followingInProgress: Array<number>
}
export const Users: React.FC<UsersFCPropsType> = (props) => {
    // let pagesCount = Math.ceil(props.totalCount /props.pageSize);
    // let pages = [];
    // for (let i = 1; i <= pagesCount; i++) {
    //     pages.push(i)
    // }
    return (
        <div>
            <Pagination totalCount={props.totalCount} pageSize={props.pageSize}
                        onPageChanged={props.onPageChanged} currentPage={props.currentPage}/>

            {props.items.map(u => {
                return (
                    <div key={u.id}>
                    <span>
                        <div>
                            <NavLink to={`/profile/${u.id}`}>
                            <img src={u.photos.small || userPhoto} className={s.usersPhoto} alt={`${u.id}`}/>
                                </NavLink>
                        </div>
                        <div>
                            {u.followed
                                ? <button
                                    disabled={props.followingInProgress.some(id => id === u.id)}
                                    onClick={() => {
                                        props.unfollow(u.id)

                                }}>Unfollow</button>
                                : <button
                                    disabled={props.followingInProgress.some(id => id === u.id)}
                                    onClick={() => {
                                        props.follow(u.id)
                                }}>Follow</button>}
                        </div>
                    </span>
                        <span>
                        <div>{u.name}</div>
                        <div>{u.status}</div>
                    </span>
                        <span>
                        <div>{"u.location.city"}</div>
                        <div>{"u.location.country"}</div>
                    </span>
                    </div>
                )

            })}
        </div>
    )
}