import React from "react";
import {connect} from "react-redux";
import {AppStateType} from "../../redux/redux-store";
import {
     followUser, getUsers,
     unfollowUser,
    UsersPageType,
} from "../../redux/users-reducer";

import {Users} from "./Users";
import {Preloader} from "../common/preloader/Preloader";
import {getUsersPage} from "../../redux/users-selectors";


export type mapStateToPropsUserPageType = {
    usersPage: UsersPageType
}
export type mapDispatchToPropsUserPageType = {
    getUsers: (currentPage: number, pageSize: number) => void
    followUser: (userId: number) => void
    unfollowUser: (userId: number) => void
}
type UsersPropsType = mapStateToPropsUserPageType & mapDispatchToPropsUserPageType

class UsersContainer extends React.Component<UsersPropsType> {
    componentDidMount() {
        this.props.getUsers(this.props.usersPage.currentPage, this.props.usersPage.pageSize)

    }
    onPageChanged = (pageNumber: number) => {
        this.props.getUsers(pageNumber, this.props.usersPage.pageSize)
    }

    render() {
        return (
            <div>
                {this.props.usersPage.isFetching ? <Preloader/> : null}
            <>
            <Users totalCount={this.props.usersPage.totalCount}
                   pageSize={this.props.usersPage.pageSize}
                   onPageChanged={this.onPageChanged}
                   currentPage={this.props.usersPage.currentPage}
                   items={this.props.usersPage.items}
                   follow={this.props.followUser}
                   unfollow={this.props.unfollowUser}
                   followingInProgress={this.props.usersPage.followingInProgress}

            />
            </>
            </div>
        )
    }
}

const mapStateToProps = (state: AppStateType): mapStateToPropsUserPageType => {
    return {
        usersPage: getUsersPage(state),
    }
}
