import {ActionsType, DialogsPageType, MessageType} from "./store";


const initialState: DialogsPageType = {
    dialogs: [
        {id: 1, name: "Sema"},
        {id: 2, name: "Yulia"},
        {id: 3, name: "Tanya"},
        {id: 4, name: "Bob"},
    ],
    messages: [
        {id: 1, message: "Hello"},
        {id: 2, message: "How are you?"},
        {id: 3, message: "I am fine"},
    ],
}
const dialogsReducer = (state:DialogsPageType = initialState, action: ActionsType): DialogsPageType => {
    switch(action.type){
        case "dialogs/ADD-MESSAGE":
            return {...state, messages: [...state.messages, {id: 3, message: action.newMessageText}]}
        default:
            return state;
    }
}
export const addMessageAC = (newMessageText: string) => {
    return {
        type: 'dialogs/ADD-MESSAGE',
        newMessageText
    } as const
}

export default dialogsReducer;
