import React from "react";
import Header from "./Header";

import {AppStateType} from "../../redux/redux-store";
import {connect} from "react-redux";
import {logOutTC, setAuthUserDataTC} from "../../redux/auth-reducer";
type MapStateToPropsType = {
    isAuth: boolean,
    name: string | null,
}
type MapDispatchToPropsType = {
    setAuthUserDataTC: () => void
    logOutTC: () => void

}
type HeaderContainerPropsType = MapStateToPropsType & MapDispatchToPropsType
class HeaderContainer extends React.Component<HeaderContainerPropsType>{
    render(){
        return <Header logOut={ this.props.logOutTC} login={this.props.name} isAuth={this.props.isAuth}/>
    }
}
const mapStateToProps = (state: AppStateType): MapStateToPropsType => {
    return {
        isAuth: state.auth.isAuth,
        name: state.auth.login,
    }
}
export default connect(mapStateToProps, {setAuthUserDataTC, logOutTC})(HeaderContainer)